<template>
  <div class="mod">
      <div id="subject_teacher_num" class="chart" :ref="chartNum"></div>
      <div class="noData" v-if="infoData.length === 0">
        <img src="../../assets/imgs/index/nodata.png" alt="">
        <p>暂无数据</p>
      </div>
    </div>
</template>
<script>
// import * as echarts from 'echarts'
import DashboardWidget from '../../../static/DashboardWidget'
import * as themeObj from '../../../static/echartsTheme/index'
import { DateUtil } from '../../utils/index'
import Api from '../../utils/api'
export default {
  data () {
    return {
      dateRange: [DateUtil.date(), DateUtil.date()],
      infoData: []
    }
  },
  props: {
    theme: {
      type: String
    },
    chartNum: {
      type: String,
      default () {
        return 'subject_teacher_num'
      }
    }
  },
  computed: {
  },
  created () {
  },
  mounted: function () {
    var that = this
    that.loadChart()
  },
  methods: {
    async getData () {
      const that = this
      return new Promise(resolve => {
        Api.School.setDate(that.dateRange[1]).getApiData('subjectTeacher', { sid: this.sid }, function (data) {
          that.infoData = data.dataset.source
          resolve(data.dataset)
        })
      })
    },
    async loadChart () {
      var that = this
      var dataset = await that.getData()
      dataset.source = dataset.source.slice(0, 5)
      // 创建实例
      const dw = new DashboardWidget(`${that.chartNum}`, {})
      // 初始化图表
      dw.initChart(that.$refs[`${that.chartNum}`])
      // 设置图表配置
      dw.setOption({
        backgroundColor: 'transparent',
        dataset: [dataset],
        title: {
          text: '{a|     职称统计}',
          show: true,
          textStyle: {
            color: '#C8CDFF',
            rich: {
              a: {
                fontSize: '100%',
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require('../../assets/imgs/title.png')
                }
              }
            }
          }
        },
         legend: {
          show: true,
           align:'top',
          orient: 'horizontal',
          icon: 'circle',
          itemWidth: 12,
          left:'0',
          top: '10%',
            formatter: function (name) {
            // var index = 0
            // var clientcounts = legendData
            // var clientlabels1 = clientlabels
            // clientlabels1.forEach(function (value, i) {
            //   if (value === name) {
            //     index = i
            //   }
            // })
            return '优秀'
          },
          textStyle: {
            color: '#C8CDFF',
            padding: [12, 0, 10, 20],
            fontSize: '80%'
          }
        },
        grid: {
          x: 30,
          y: 60,
          x2: 30,
          y2: 55
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: false
          },
          axisLine: {
            show: true
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            rotate: 0,
            interval: 0,
            fontSize: '66.7%',
            color: '#919BFF'
          }
        },
        yAxis: {
          type: 'value',
          // interval: 10,
          minInterval: 1,
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: 'dashed',
              color: '#4F5293'
            }
          },
          axisLabel: {
            fontSize: '66.7%',
            color: '#919BFF'
          }
        },
        tooltip: {},
        series: [{
          type: 'bar',
          center: ['20%', '80%'], // 图的位置，距离左跟上的位置
          barMaxWidth: '30%', // 柱图宽度
          encode: {
            x: 'subject',
            y: 'teacher_num'
          },
          itemStyle: {
            normal: {
              label: {
                show: true, // 开启显示
                position: 'top', // 在上方显示
                textStyle: { // 数值样式
                  color: '#999999',
                  fontSize: '65%'
                }
              },
              // 定义一个list，然后根据所以取得不同的值，这样就实现了，
              color: function (params) {
                // 设置主题
                let theme = ''
                for (var i in themeObj) {
                  if (that.theme == themeObj[i].type) {
                    theme = themeObj[i].color
                  } else {
                    theme = themeObj.defaultTheme.color
                  }
                }
                return theme[params.dataIndex]
                // var colorList = that.theme === 'lightTheme' ? lightTheme.color : defaultTheme.color
              }
            }
          }
        }]
      })
      // 把方法赋值给vue（注意通过bind改变this指向）
      that.drawChart = dw.drawChart.bind(dw)
      that.resize = dw.resize.bind(dw)
      // 在虚拟dom创建好后 绘制图表
      that.$nextTick(function () {
        that.drawChart()
      })
    },
    drawChart () {},
    resize () {}
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.mod{
  width: 100%;
  height: 100%;
  position: relative;
}
.chart {
    box-sizing:border-box;
    // padding: 0.2rem;
    width: 100%;
    height: 100%
}
</style>
