<template>
<div class="radar_map">
    <div id="radar_map" class="chart" :ref="chartNum"></div>
    <!-- <div v-if="infoData.length === 0" class="noData">
      <img src="../../assets/imgs/index/nodata.png" alt="">
      <p>暂无数据</p>
    </div> -->
</div>
</template>

<script>
// import * as echarts from 'echarts'
import DashboardWidget from '../../../static/DashboardWidget'
import * as themeObj from '../../../static/echartsTheme/index'
import Api from '../../utils/api'
export default {
  name: 'EyesightRateLeft',
  data () {
    return {
      themeColor: '',
      myChart: '',
      infoData: []
    }
  },
  props: {
    theme: {
      type: String
    },
    chartNum: {
      type: String,
      default () {
        return 'radar_map'
      }
    },
    batch: {
      type: String
    },
    gradeId: {
      type: String
    }
  },
  computed: {
  },
  created () {
    this.getData()
  },
  watch: {
    batch () {
      this.loadChart()
    },
    gradeId () {
      this.loadChart()
    }
  },
  mounted: function () {
    var that = this
    that.loadChart()
  },
  methods: {
    async getData () {
      const that = this
      const params = { sid: this.sid }
      if (that.batch) {
        params.batch_number = that.batch
      }
      if (that.gradeId) {
        params.grade_id = that.gradeId
      }
      return new Promise(resolve => {
        Api.EyeHealth.getApiData('visionRange', params, function (data) {
          that.infoData = data.source
          resolve(data)
        })
      })
    },
    async loadChart () {
      var that = this
      var dataset = await that.getData()
      // 创建实例
      const dw = new DashboardWidget(`${that.chartNum}`, {})
      // 设置主题
      let theme = ''
      for (var i in themeObj) {
        if (that.theme == themeObj[i].type) {
          theme = themeObj[i]
        } else {
          theme = themeObj.defaultTheme
        }
      }
      // 初始化图表
      dw.initChart(that.$refs[`${that.chartNum}`], theme)
      // 设置图表配置
      dw.setOption({
        backgroundColor: 'transparent',
        dataset: [dataset],
        tooltip: {
          formatter: function (data) {
            return 'ss'
            // return data.data[0] + ' ' + data.percent + '%'
          }
        },
        title: {
          text: '{a|      学校类型}',
          show: true,
          textStyle: {
            color: '#C8CDFF',
            rich: {
              a: {
                fontSize: '100%',
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require('../../assets/imgs/title.png')
                }
              }
            }
          }
        },

          legend: {
              left: 'center',
              data: ['某软件', '某主食手机', '某水果手机', '降水量', '蒸发量']
          },
             radar: [
       
        {
            indicator: [
                {text: '外观', max: 100},
                {text: '拍照', max: 100},
                {text: '系统', max: 100},
                {text: '性能', max: 100},
                {text: '屏幕', max: 100}
            ],
            radius: 80,
            center: ['50%', '60%'],
        }
    ],
        series: [{
          type: 'radar',
          data: [
                {
                    value: [60, 73, 85, 40],
                    name: ''
                }
            ]
        }]
      })
      // 把方法赋值给vue（注意通过bind改变this指向）
      that.drawChart = dw.drawChart.bind(dw)
      that.resize = dw.resize.bind(dw)
      // 在虚拟dom创建好后 绘制图表
      that.$nextTick(function () {
        that.drawChart()
      })
    },
    drawChart () {},
    resize () {}
  }
}
</script>

<style lang='scss' scoped>
$commonColor: #C8CDFF;
$themeColor: #919BFF;
$textColore:#E768FF;
$textColor1:#1CFBEC;
$textColor6:#6A65FF;
.textColore{
    color: $textColore;
}
.textColor1{
    color: $textColor1;
}
.textColor6{
    color: $textColor6;
}

.radar_map{
  width: 100%;
  height: 100%;
  box-sizing:border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  .info{
        display: flex;
        justify-content: center;
        .male{
            margin-right: 0.7rem;
            display: flex;
            color: #C8CDFF;
            font-size: 0.12rem;
            .num{
                  color: $textColor1;
                 span{
                font-size: 0.24rem;
            }
            }
           
        }
        .female{
            display: flex;
             color: #C8CDFF;
            font-size: 0.12rem;
            .num{
                color: #6472FF;
                span{
                 font-size: 0.24rem;
                    }
                }
            }
           
        
  }



}
.title {
    padding-bottom: 0.2rem;
    font-size: 0.14rem;
    color: $commonColor;
    img {
        width: 0.15rem;
        height: 0.14rem;
        margin-right: 0.06rem;
    }
    span {
        font-size: 0.18rem;
    }
    .seven{
      margin-left: 0.06rem;
      font-size: 0.12rem;
    }
}


.chart {
    box-sizing:border-box;
    width: 100%;
    height: 3.2rem;
}


.el-carousel {
  /deep/.el-carousel__indicator--horizontal {
    margin: 0 !important;
    padding: 0 !important;
    margin: 0 0 0 0.08rem !important;
    .el-carousel__button {
      width: 0.06rem !important;
      height: 0.06rem !important;
      border-radius: 0.06rem !important;
    }
    .el-carousel__button
      /deep/.el-carousel__indicator.is-active
      button {
      background-color: $commonColor !important;
    }
  }
  /deep/.el-carousel__indicator--vertical {
    margin: 0 !important;
    padding: 0 !important;
    // margin: 0 0 0 0.04rem !important;
    .el-carousel__button {
      width: 0.04rem !important;
      height: 0.8rem !important;
      background-color: #6472FF !important;
    }
    .el-carousel__button:nth-child(1){
      border-radius: 0.04rem 0.04rem 0 0 !important;
    }
    .el-carousel__button:nth-last-child(1){
      border-radius: 0 0 0.04rem 0.04rem !important;
    }
    // .el-carousel__indicator .el-carousel__indicator--vertical is-active{
    //   button {
    //     background-color: #6472FF !important;
    //   }
    // }
    .el-carousel__button{
      /deep/.el-carousel__indicator .is-active button {
        background-color: #6472FF !important;
      }
    }
  }
}
</style>
