<template>
  <div class="regionTeachersStatistics">
    <div class="title">
      <span>全区教师人数统计</span>
    </div>
    <div class="bottom-wrap">
      <div class="left">
        <div class="left-top">
          <div class="num">
            <span>{{ total }}</span
            >人
          </div>
          <div class="ch">教师总人数</div>
          <div class="eh">The number of student</div>
        </div>
        <div class="left-bottom">
          <div v-show="isShowChart1" class="chart" :ref="chartName1"></div>
        </div>
      </div>
      <div class="middle">
        <div class="title">
          <img src="../../assets/imgs/overview/title.png" alt="" />
          <span>性别占比</span>
        </div>
        <div class="info">
          <div class="male">
            <div><i class="iconfont">&#xe7f5;</i></div>
            <div>
              男
              <div class="num">
                <span>{{ man }}</span
                >人
              </div>
            </div>
          </div>
          <div class="female">
            <div><i class="iconfont">&#xe7f3;</i></div>
            <div>
              女
              <div class="num">
                <span>{{ woman }}</span
                >人
              </div>
            </div>
          </div>
        </div>
        <div class="bottom">
          <div v-show="isShowChart2" class="chart" :ref="chartName2"></div>
        </div>
      </div>

      <div class="right">
        <div v-show="isShowChart3" class="chart" :ref="chartName3"></div>
        <div v-if="!isShowChart3" class="noData">
          <img src="../../assets/imgs/index/nodata.png" alt="" />
          <p>暂无数据</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as themeObj from "../../../static/echartsTheme/index";
import * as echarts from "echarts";
import Api from "../../utils/api";
import DW from "../../../static/lib/DW";
export default {
  props: {
    theme: {
      type: String,
    },
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      sid: "1120303",
      chartName1: this.chartName + "1",
      chartName2: this.chartName + "2",
      chartName3: this.chartName + "3",
      chart1: this.chartName + "1",
      chart2: this.chartName + "2",
      chart2: this.chartName + "3",
      isShowChart1: true,
      isShowChart2: true,
      isShowChart3: true,
      dw: null,
      provider_id: this.provider_id,
      district_id: this.district_id,
      id: this.id, //	区域id 或学校id
      type: 1, //区域1 学校2
      user_type: 2, //0全部 1学生 2老师
      total: 0, //总人数
      man: 0, //男生人数
      woman: 0, //女生人数
    };
  },
  created() {
    this.getStudentSexProportion();
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },
  methods: {
    //性别占比/学校人口分析/教师总人口/学生总人扣/教师信息分析(学校、主管部门、学校数据分析)
    async getStudentSexProportion() {
      const that = this;
      return new Promise((resolve) => {
        Api.StudentSexProportion.getApiData(
          "StudentSexProportion",
          {
            provider_id: this.provider_id,
            id: this.id,
            type: this.type,
            user_type: this.user_type,
          },
          function (data) {
            const { man, total, woman } = data;
            that.total = total;
            that.man = man;
            that.woman = woman;
          }
        );
      });
    },

    // 教师年龄段统计
    async getAgeData() {
      const that = this;
      return new Promise((resolve) => {
        Api.teacherAgeCensus.getApiData(
          "teacherAgeCensus",
          { provider_id: this.provider_id, id: this.id, type: this.type },
          function (data) {
            resolve(data.list);
          }
        );
      });
    },

    // 教师职称占比
    async getTeacherTitleCensus() {
      const that = this;
      return new Promise((resolve) => {
        Api.TeacherTitleCensus.getApiData(
          "TeacherTitleCensus",
          { provider_id: this.provider_id, id: this.id, type: this.type },
          function (data) {
            resolve(data);
          }
        );
      });
    },

    async _drawLine() {
      let that = this;
      let dataset = await that.getAgeData();
      if (dataset.source.length === 0) {
        that.isShowChart1 = false;
      }
      let dataset3 = await that.getTeacherTitleCensus();
      if (dataset3.source.length === 0) {
        that.isShowChart3 = false;
      }

      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`]
        // that.dw.getTheme()
      );

      // 设置图表配置
      const clientlabels = [];
      const indicator = [];
      let sortSoure =  dataset.source.sort((a,b)=>{
        return b[1]-a[1]
      })
      let max = sortSoure[0][1]

      dataset.source.forEach((item) => {
        indicator.push({ text: item[0] ,max:max});
        clientlabels.push(item[1]);
      });
      this.chart1.setOption({
        //getDataset获取数据；
        // dataset: [that.dw.getDataset()[0]],
          backgroundColor: "transparent",
        dataset: [dataset],
        color: ["#6A65FF"],
        title: {
          text: "{a|      年龄段分布}",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            rich: {
              a: {
                fontSize: "100%",
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require("../../assets/imgs/title.png"),
                },
              },
            },
          },
        },
        tooltip: {},
        legend: {
          show: false,
          left: "center",
        },
        radar: [
          {
            indicator: indicator,
            center: ["50%", "60%"],
            name: {
              textStyle: {
                color: "#6472FF",
              },
            },
              axisLine: {
            lineStyle: {
              color: "rgba(76, 82, 90, 1)",
            },
          },
      splitArea: {
            show: false,
            areaStyle: {
              color: "rgba(255,0,0,0)", // 图表背景的颜色
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              width: 1,
              color: "#424852", // 设置网格的颜色
            },
          },
          },
        ],
        series: [
          {
            type: "radar",
            data: [
              {
                 symbol: "circle",
                symbolSize: 5,
                value: clientlabels,
                areaStyle: { color: "rgba(106, 101, 255, .77)" },
                itemStyle: {
                  normal: {
                    borderWidth: 1,
                    color: "rgba(255, 255, 255, 1)",
                    borderColor: "rgba(100, 114, 255, 1)",
                  },
                },
                lineStyle: {
                  color: "rgba(106, 101, 255, 0.4)",
                  width: 2,
                },
                name: "年龄段分布",
              },
            ],
          },
        ],
      });

      this.chart2 = echarts.init(
        that.$refs[`${that.chartName2}`]
        // that.dw.getTheme()
      );

      this.chart2.setOption({
        color: ["#03F7FF", "#6472FF"],
        title: {
          text: "教师分布",
          top: "45%",
          textAlign: "center",
          left: "49%",
          textStyle: {
            color: "#C8CDFF",
            fontSize: 12,
            fontWeight: "400",
          },
        },
        tooltip: {},
        series: [
          {
            name: "性别占比",
            type: "pie",
            radius: ["24%", "43%"],
            center: ["50%", "50%"],
            roseType: "radius",
            label: {
              show: true,
              formatter: function (params) {
                if (that.total) {
                  return params.name + Math.round((params.value / that.total*100)) + "%";
                }
                return params.name + "  0%";
              },
            },
            emphasis: {
              label: {
                show: true,
              },
            },
            itemStyle: {
              shadowBlur: 1,
              shadowColor: 2,
              shadowOffsetX: 5,
              shadowOffsetY: 5,
            },
            data: [
              { value: that.man, name: "男" },
              { value: that.woman, name: "女" },
            ],
          },
        ],
      });

      this.chart3 = echarts.init(
        that.$refs[`${that.chartName3}`],
        that.dw.getTheme()
      );
      this.chart3.setOption({
        dataset: [dataset3],
        backgroundColor: "transparent",
        color: ["#029BDA", "#3B478F", "#8593D0", "#51C1E7", "#24C39D"],
        tooltip: {
          trigger: "item",
        },
        title: {
          text: "{a|      教师职称占比}",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            rich: {
              a: {
                fontSize: "100%",
                width: 15,
                height: 14,
                backgroundColor: {
                  image: require("../../assets/imgs/title.png"),
                },
              },
            },
          },
        },
        grid: {
          x: 0,
          y: 0,
          x1: 0,
          y1: 0,
        },
        series: [
          {
            name: "",
            type: "funnel",
            left: "10%",
            top: 60,
            x2: 80,
            bottom: 60,
            width: "80%",
            // height: {totalHeight} - y - y2,
            min: 0,
            max: 100,
            minSize: "0%",
            maxSize: "100%",
            sort: "descending",
            gap: 2,
            label: {
              show: true,
              position: "inside",
            },
            labelLine: {
              length: 10,
              lineStyle: {
                width: 1,
                type: "solid",
              },
            },
            itemStyle: {
              borderColor: "#fff",
              borderWidth: 1,
            },
            emphasis: {
              label: {
                fontSize: 20,
              },
            },
            encode: {
              value: 1,
              itemName: 0,
            },
          },
        ],
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
$commonColor: #c8cdff;
$themeColor: #919bff;
$textColore: #e768ff;
$textColor1: #1cfbec;
$textColor6: #6a65ff;
.regionTeachersStatistics {
  .title {
    font-size: 0.16rem;
    color: #c8cdff;
    padding-top: 0.1rem;
  }
  padding: 0.2rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: url("../../assets/imgs/school/wrap_l.png") no-repeat top
    center/100% 100%;
  display: flex;
  flex-direction: column;
  .bottom-wrap {
    display: flex;
    flex: 1;
    > div {
      flex: 1;
    }
    .left {
      display: flex;
      flex-direction: column;
      .left-top {
        padding: 0.1rem 0;
        margin: 0.1rem 0;
        height: 1.2rem;
        background-color: rgba($color: #6472ff, $alpha: 0.1);
        border-radius: 2px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .num {
          font-size: 0.14rem;
          color: #8488b3;
          span {
            font-family: zcoolqingkehuangyouti;
            color: #c8cdff;
            font-size: 0.48rem;
          }
        }
        .ch {
          font-size: 0.16rem;
          color: #e5e5e5;
        }
        .eh {
          font-size: 0.12rem;
          color: #8488b3;
        }
      }
      .left-bottom {
        flex: 1;
      }
    }
    .middle {
      margin: 0 0.4rem;
      display: flex;
      flex-direction: column;
      .title {
        padding: 0.2rem 0;
        font-size: 0.18rem;
        color: $commonColor;
        img {
          width: 0.15rem;
          height: 0.14rem;
          margin-right: 0.06rem;
        }
        span {
          font-size: 0.18rem;
        }
        .seven {
          margin-left: 0.06rem;
          font-size: 0.12rem;
        }
      }
      .info {
        display: flex;
        justify-content: center;
        padding: 0.4rem 0 0.2rem 0;
        i.iconfont {
          font-size: 0.4rem;
          margin-right: 0.12rem;
        }
        .male {
          margin-right: 0.7rem;
          display: flex;
          color: #c8cdff;
          font-size: 0.12rem;
          .iconfont {
            color: #06d2c1;
          }
          .num {
            color: $textColor1;
            span {
              font-size: 0.24rem;
            }
          }
        }
        .female {
          display: flex;
          color: #c8cdff;
          font-size: 0.12rem;
          .iconfont {
            color: #6472ff;
          }
          .num {
            color: #6472ff;
            span {
              font-size: 0.24rem;
            }
          }
        }
      }
      .bottom {
        flex: 1;
      }
    }
    .right {
      position: relative;
    }
  }
}

.chart {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
</style>
