<template>
  <div :ref="chartName" class="schoolType">
    <div class="title">
      <span>学校类型</span>
    </div>
    <div class="bottom">
      <div v-show="isShowChart1" class="chart" :ref="chartName1"></div>
       <div v-if="!isShowChart1" class="noData">
      <img src="../../assets/imgs/index/nodata.png" alt="">
      <p> {{ errMSg || '暂无数据' }} </p>
    </div>
    </div>

   
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
//继承的基类
import DW from "../../../static/lib/DW";

export default {
  components: {},
  data() {
    return {
      chartName1: this.chartName + "1",
      chart1: this.chartName + "1",
      isShowChart1:true,
      dw: null,
      provider_id: this.provider_id,
      district_id: this.district_id,
      sid: this.sid,
      schools: [],
      errMSg:'',
      schoolColors: [
        "#06D2C1",
        "#FF8D8E",
        "#6472FF",
        "#FEBC31",
        "#A8ADC9",
        "#919BFF",
      ],
    };
  },
  watch: {
    $route() {},
  },
  props: {
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  computed: {},

  methods: {
    async getData() {
      const that = this;
      return new Promise((resolve) => {
        Api.SchoolTypeCensus.getApiData(
          "SchoolTypeCensus",
          { provider_id: this.provider_id, district_id: that.district_id },
          function (data) {
            that.schools = [];
            for (var j = 0; j < data.list.source.length; j += 6) {
              that.schools.push(...data.list.source.slice(j, j + 6));
            }
            resolve(data.list);
          },
          function(err){
            console.log(err)
            if(err==undefined){
              that.errMSg = '获取数据错误'
              that.isShowChart1 = false
            }
          }
        );
      });
    },
    async _drawLine() {
      let that = this;
      //getTheme获取主题；
       let dataset = await that.getData();
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );
     
      this.chart1.setOption({
        //getDataset获取数据；
        // dataset: [that.dw.getDataset()[0]],
        dataset: [dataset],
        backgroundColor: "transparent",
        color: [
          "#FF7723",
          "#6472FF",
          "#FF8080",
          "#FFFFFF",
          "#06D28B",
          "#F5BA3F",
        ],
        tooltip: {},
        legend: {
          alight: "center",
          top: "28%",
          orient: "vertical",
          itemWidth: 10,
          itemHeight: 5,
          show: true,
          // top: 'center',
          left: "60%",
          orient: "vertical",
          formatter: function (p) {
            return p
          },
          textStyle: {
            color: "#FFFFFF",
            rich: {
              a: {
                lineHeight: 10,
                fontSize:16
              },
            },
          },
        },
        series: [
          {
            type: "pie",
            center: ["40%", "50%"],
            radius: ["30%", "50%"],
            avoidLabelOverlap: false,
            // encode: {
            //   value: 'name: ',
            //   itemName: 'name',
            //   tooltip: ['name-num',]
            // },
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "12",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
          },
        ],
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须

    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },
};
</script>
<style lang="scss" scoped>
$commonColor: #c8cdff;
$themeColor: #919bff;
$textColorg: #06d2c1;
$textColorr: #ff8d8e;
$textColorb: #6472ff;
.textColorg {
  color: $textColorg;
}
.textColorr {
  color: $textColorr;
}
.textColorb {
  color: $textColorb;
}
.schoolType {
  width: 100%;
  height: 100%;
  background: url("../../assets/imgs/school/wrapschool.png") no-repeat top
    center/100% 100%;
  box-sizing: border-box;
  padding: 0.2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  .type {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .box {
      width: 1.46rem;
      //   height: 0.8rem;
      font-size: 0.16rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 0.2rem;
      justify-content: center;
      // padding: 0.06rem 0;
      box-sizing: border-box;
      background: url("../../assets/imgs/border.png") no-repeat top center/100%
        100%;
      .numbs {
        font-size: 0.48rem;
        font-family: "ping-Huangyou" !important;
      }
      .mold {
        font-size: 0.12rem;
        color: #c8cdff;
      }
    }
  }
}
.title {
  padding: 0.2rem 0;
  font-size: 0.18rem;
  color: $commonColor;
  img {
    width: 0.15rem;
    height: 0.14rem;
    margin-right: 0.06rem;
  }
  span {
    font-size: 0.18rem;
  }
  .seven {
    margin-left: 0.06rem;
    font-size: 0.12rem;
  }
}
.bottom {
  flex: 1;
  width: 100%;
  //   height: 1.6rem;
}

.chart {
  box-sizing: border-box;
  padding-top: 0.1rem;
  width: 100%;
  height: 100%;
}

.el-carousel {
  /deep/.el-carousel__indicator--horizontal {
    margin: 0 !important;
    padding: 0 !important;
    margin: 0 0 0 0.08rem !important;
    .el-carousel__button {
      width: 0.06rem !important;
      height: 0.06rem !important;
      border-radius: 0.06rem !important;
    }
    .el-carousel__button /deep/.el-carousel__indicator.is-active button {
      background-color: $commonColor !important;
    }
  }
  /deep/.el-carousel__indicator--vertical {
    margin: 0 !important;
    padding: 0 !important;
    // margin: 0 0 0 0.04rem !important;
    .el-carousel__button {
      width: 0.04rem !important;
      height: 0.8rem !important;
      background-color: #6472ff !important;
    }
    .el-carousel__button:nth-child(1) {
      border-radius: 0.04rem 0.04rem 0 0 !important;
    }
    .el-carousel__button:nth-last-child(1) {
      border-radius: 0 0 0.04rem 0.04rem !important;
    }
    // .el-carousel__indicator .el-carousel__indicator--vertical is-active{
    //   button {
    //     background-color: #6472FF !important;
    //   }
    // }
    .el-carousel__button {
      /deep/.el-carousel__indicator .is-active button {
        background-color: #6472ff !important;
      }
    }
  }
}
</style>
