
import * as echarts from 'echarts'

export default class DashboardWidget {
  /*
    * 构造一个vue图表组件
    * @ref: vue的ref，类似dom
    * @config: todo其他配置项
    * */
  constructor (ref, config) {
    this.ref = ref
    this.config = config
    this._option = {}
    this._chart = ''
  }

  /*
    * init一个图表的实例
    * */
  initChart (dom, theme) {
    this._chart = echarts.init(dom, theme)
    // this._chart = chart
  }

  destroyChart (dom, theme) {
    this._chart = echarts.init(dom).dispose()
    this._chart = echarts.init(dom, theme)
    // this._chart = chart
  }

  /*
    * 设置图表option
    * */
  setOption (option) {
    this._option = option
    // this._option.backgroundColor = 'transparent'
  }

  /*
     * 图表绘制
     * */
  drawChart () {
    const that = this
    if (!this._chart) {
      throw Error('请先通过 initChart init图表')
    }
    if (!this._option) {
      throw Error('请先通过 setOption 设置图表option')
    }
    this._chart.setOption(that._option)
  }

  /*
    * 图表重绘
    * */
  resize () {
    console.log(this._chart)
    this._chart.resize()
  }
}

// export default DashboardWidget
