<!-- 设备统计的组件 -->
<template>
  <div class="equipmentClassification" :ref="chartName">
    <div class="title">
      <img src="../../assets/imgs/overview/title.png" alt="" />
      <span>设备分类及统计</span>
    </div>
    <!-- <span v-if="deviceStatistics.length === 0" class="no-data">暂无数据</span> -->
    <!-- <div class="noData" v-if="deviceStatistics.length === 0">
      <img src="../../assets/imgs/index/nodata.png" alt="">
      <p>暂无数据</p>
    </div> -->
    <!-- swiper -->
    <div class="swiperBox">
        <div v-if="deviceStatistics.length === 0" class="noData">
        <img src="../../assets/imgs/index/nodata.png" alt="">
        <p>暂无数据</p>
      </div>
      <el-carousel :interval="5000" arrow="never" indicator-position="outside">
        <el-carousel-item
          class="swiper"
          v-for="(item, index) in deviceStatistics"
          :key="index"
        >
          <div v-for="(v, i) in item" :key="i">
            <div class="itemBox">
              <!-- <img :src="v[0]" alt=""> -->
              <img
                v-show="v['type'] == 1"
                src="../../assets/imgs/overview/1.png"
                alt=""
              />
              <img
                v-show="v['type'] == 2"
                src="../../assets/imgs/overview/2.png"
                alt=""
              />
              <img
                v-show="v['type'] == 5"
                src="../../assets/imgs/overview/5.png"
                alt=""
              />
              <img
                v-show="v['type'] == 10"
                src="../../assets/imgs/overview/10.png"
                alt=""
              />
              <img
                v-show="v['type'] == 11"
                src="../../assets/imgs/overview/11.png"
                alt=""
              />
              <img
                v-show="
                  v['type'] !== 1 &&
                  v['type'] !== 2 &&
                  v['type'] !== 5 &&
                  v['type'] !== 10 &&
                  v['type'] !== 11
                "
                src="../../assets/imgs/overview/default.png"
                alt=""
              />
              <div>
                <p class="item">{{ v["name"] }}</p>
                <p class="item">
                  <span>{{ v["total"] }}</span>
                  <span>台</span>
                </p>
              </div>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>

    </div>
    <div class="bottom">
      <div v-if="!isShowChart1" class="noData">
        <img src="../../assets/imgs/index/nodata.png" alt="">
        <p>暂无数据</p>
      </div>
      <div v-show="isShowChart1" class="chart" :ref="chartName1"></div>
      
    </div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
import DW from "../../../static/lib/DW";

export default {
  components: {},
  data() {
    return {
      deviceStatistics: [],
      chartName1: this.chartName + "1",
      chart1: this.chartName + "1",
      provider_id: this.provider_id,
      district_id: this.district_id,
      dw: null,
      isShowChart1:true
    };
  },

  wacth: {},
  props: {
    theme: {
      type: String,
    },
    chartName: {
      type: String,
    },
    base_url: {
      type: String,
      default: "",
    },
    token: {
      type: String,
      default: "",
    },
  },
  created() {
    this.getData();
  },
  mounted: function () {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须

    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function () {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise((resolve) => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function () {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function () {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" },
      ];
    };
  },

  computed: {},

  methods: {
    async getData() {
      const that = this;
      return new Promise((resolve) => {
        Api.DeviceClassify.getApiData(
          "DeviceClassify",
          { provider_id: this.provider_id, district_id: that.district_id },
          function (data) {
            that.deviceStatistics = [];
            for (var j = 0; j < data.device.length; j += 6) {
              that.deviceStatistics.push(data.device.slice(j, j + 6));
            }
            that.school_device = data.school_device;
            resolve([data.school_device, data.device]);
          }
        );
      });
    },
    async _drawLine() {
      let that = this;
      let dataset = await that.getData();

      that.infoData = dataset[1];
      // console.log(dataset)
      const device = dataset[1];
      const school_device = dataset[0];

      if(school_device.length===0){
        that.isShowChart1 =false

      }
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        that.dw.getTheme()
      );

      // 设置图表配置
      const schollName = [];
      const seriesData = [];
      school_device.forEach((item, i) => {
        schollName.push(item.school_name);
      });
      device.forEach((item, i) => {
        let o = {};
        school_device.forEach((si, i) => {
          si.list.forEach((li) => {
            if (item.type == li.type) {
              if (o[item.type]) {
                o[item.type].push(item.total);
              } else {
                o[item.type] = [item.total];
              }
            }
          });
        });
        seriesData.push({
          name: item.name,
          type: "bar",
          stack: "total",
          label: {
            show: false,
          },
          emphasis: {
            focus: "series",
          },
          data: o[item.type],
        });
      });
      this.chart1.setOption({
        backgroundColor: "transparent",
        color:['#C768B9','#8EC662','#FFCF62','#F47F78','#97D3F0'],
        title: {
          text: "教育装备前八学校",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            fontSize: 12,
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // Use axis to trigger tooltip
            type: "shadow", // 'shadow' as default; can also be 'line' or 'shadow'
          },
        },
        legend: {
          // data: ["智能闸机", "智能闸机", "智能闸机", "智能闸机", "智能闸机"],
          show: true,
          top: "20%",
          type:'scroll',
            itemWidth: 10,
        itemHeight: 10,
        itemGap: 10,
        },
        grid: {
          top: "50%",
          left: "3%",
          right: "4%",
          bottom: "0%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          show: false,
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "category",
          data: schollName,
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
          },
        },
        series: seriesData,
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function (el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    },
  },
};
</script>
<style lang="scss" scoped>
$commonColor: #c8cdff;
$themeColor: #919bff;

.bottom {
  position: relative;
  flex: 1;
}
.chart {
  box-sizing: border-box;
  // padding-top: 0.2rem;
  width: 100%;
  height: 100%;
}
.equipmentClassification {
  width: 100%;
  height: 100%;
  background: url("../../assets/imgs/school/wrapschool.png") no-repeat top
    center/100% 100%;
  box-sizing: border-box;
  padding: 0.2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  .swiperBox {
    position: relative;
    // margin: 0 0.2rem;
    height: 2.6rem;
    .el-carousel {
      height: 100%;
    }
    /deep/.el-carousel__container {
      height: 77% !important;
    }
    .swiper {
      width: 6rem;
      height: 100%;
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;
    }
    .itemBox {
      width: 1.4rem;
      height: 0.7rem;
      padding: 0.1rem;
      background-color: #191d36;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0.2rem 0.2rem 0;
      img {
        width: 0.36rem;
        height: 0.36rem;
        margin-right: 0.1rem;
      }
      > div {
        font-size: 0;
        .item:nth-child(1) {
          font-size: 0.12rem;
          color: $commonColor;
        }
        .item:nth-child(2) {
          color: #06d2c1;
          span:nth-child(1) {
            font-size: 0.28rem;
            margin-right: 0.08rem;
          }
          span:nth-child(2) {
            font-size: 0.16rem;
          }
        }
      }
    }
  }
}
.title {
  // margin: 0 0 0.2rem 0.2rem;
  padding: 0.2rem 0;
  font-size: 0.18rem;
  color: $commonColor;
  img {
    width: 0.15rem;
    height: 0.14rem;
    margin-right: 0.06rem;
  }
  span {
    font-size: 0.18rem;
  }
}
._title {
  margin: 0.2rem 0 0.2rem 0.2rem;
  font-size: 0.18rem;
  color: #6472ff;
  img {
    width: 0.08rem;
    height: 0.12rem;
    margin-right: 0.06rem;
  }
  span {
    font-size: 0.14rem;
  }
}

.el-carousel {
  /deep/.el-carousel__indicator--horizontal {
    margin: 0 !important;
    padding: 0 !important;
    margin: 0 0 0 0.08rem !important;
    .el-carousel__button {
      width: 0.06rem !important;
      height: 0.06rem !important;
      border-radius: 0.06rem !important;
    }
    .el-carousel__button /deep/.el-carousel__indicator.is-active button {
      background-color: $commonColor !important;
    }
  }
  /deep/.el-carousel__indicator--vertical {
    margin: 0 !important;
    padding: 0 !important;
    // margin: 0 0 0 0.04rem !important;
    .el-carousel__button {
      width: 0.04rem !important;
      height: 0.8rem !important;
      background-color: #6472ff !important;
    }
    .el-carousel__button:nth-child(1) {
      border-radius: 0.04rem 0.04rem 0 0 !important;
    }
    .el-carousel__button:nth-last-child(1) {
      border-radius: 0 0 0.04rem 0.04rem !important;
    }
    // .el-carousel__indicator .el-carousel__indicator--vertical is-active{
    //   button {
    //     background-color: #6472FF !important;
    //   }
    // }
    .el-carousel__button {
      /deep/.el-carousel__indicator .is-active button {
        background-color: #6472ff !important;
      }
    }
  }
}
</style>
