<template>
  <div class="studentNumberStatistics">
    <div class="title">
      <span>学生人数统计</span>
    </div>
    <div class="wrap">
      <div class="wrap-left">
        <div class="wrap-left-top">
          <div class="num"><span>{{total}}</span>人</div>
          <div class="ch">学生总人数</div>
          <div class="eh">
            The number of student
          </div>
        </div>
        <!-- <div class="wrap-left-center">
          <div>
            <i>en</i>
            <div>
              <div class="num textColore">874526</div>
              <div class="txt">小学总人数</div>
            </div>
          </div>
          <div>
            <i>en</i>
            <div>
              <div class="num textColor1">874526</div>
              <div class="txt">中学总人数</div>
            </div>
          </div>
          <div>
            <i>en</i>
            <div>
              <div class="num textColor6">874526</div>
              <div class="txt">大学总人数</div>
            </div>
          </div>
        </div> -->
        <div class="wrap-left-bottom">
          <div v-show="isShowChart1" class="chart" :ref="chartName1"></div>
          <div v-if="!isShowChart1" class="noData">
              <img src="../../assets/imgs/index/nodata.png" alt="">
              <p>暂无数据</p>
            </div>
        </div>
      </div>
      <div class="wrap-right">
        <div class="title">
          <img src="../../assets/imgs/overview/title.png" alt="" />
          <span>性别占比</span>
        </div>
        <div class="info">
          <div class="male">
            <div>
              <i class="iconfont">&#xe7f5;</i>
            </div>
            <div>
              男生人数
              <div class="num"><span>{{man}}</span>人</div>
            </div>
          </div>
          <div class="female">
            <div>
              <i class="iconfont">&#xe7f3;</i>
            </div>
            <div>
              女生人数
              <div class="num"><span>{{woman}}</span>人</div>
            </div>
          </div>
        </div>
        <div class="right-bottom">
          <div v-show="isShowChart2" class="chart" :ref="chartName2"></div>
           <div v-if="!isShowChart2" class="noData">
              <img src="../../assets/imgs/index/nodata.png" alt="">
              <p>暂无数据</p>
            </div>

        </div>
      </div>
    </div>
  
  </div>
</template>

<script>
import * as themeObj from "../../../static/echartsTheme/index";
import * as echarts from "echarts";
import Api from "../../utils/api";
import DW from "../../../static/lib/DW";
export default {
  components: {},
  props: {
    theme: {
      type: String
    },
    chartName: {
      type: String
    },
    base_url: {
      type: String,
      default: ""
    },
    token: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      uid: "1120303",
      chartName1: this.chartName + "1",
      chartName2: this.chartName + "2",
      chart1: this.chartName + "1",
      isShowChart1:true,
      isShowChart2:true,
      chart2: this.chartName + "2",
      total:0,//总人数
      man:0,//男生人数
      woman:0,//女生人数
      dw: null,
      sid: this.sid,
      provider_id: this.provider_id,
      district_id: this.district_id,
      id:this.id,//	区域id 或学校id
      type:1,//区域1 学校2
      user_type:1,//0全部 1学生 2老师	
    };
  },

  created() {
      this.getStudentSexProportion();
  },
  mounted: function() {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function() {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise(resolve => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function() {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function() {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" }
      ];
    };
  },
  computed: {},

  methods: {
    // 学校人数统计
    async getEducationSchoolCensus() {
      const that = this;
      return new Promise(resolve => {
        Api.EducationSchoolCensus.getApiData(
          "EducationSchoolCensus",
          { 
            provider_id:that.provider_id,
            district_id:that.district_id
          },
          function(data) {
            resolve(data.list);
          }
        );
      });
    },
    //性别占比/学校人口分析/教师总人口/学生总人扣/教师信息分析(学校、主管部门、学校数据分析)
    async getStudentSexProportion() {
      const that = this;
      return new Promise(resolve => {
        Api.StudentSexProportion.getApiData(
          "StudentSexProportion",
          { provider_id: this.provider_id, id: this.id,type:this.type,user_type:this.user_type  },
          function(data) {
          const { man,total,woman} = data;
          that.total  = total;
          that.man = man;
          that.woman =woman ;
          }
        );
      });
    },

    async _drawLine() {
      let that = this;
      let dataset = await that.getEducationSchoolCensus();
      if(dataset.source.length===0){
        that.isShowChart1 = false
      }
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`]
        // that.dw.getTheme()
      );

      // 设置图表配置
      // const clientlabels = [];
      // const legendData = [];
      // dataset.source.forEach(item => {
      //   clientlabels.push(item[0]);
      //   legendData.push(item[1]);
      // });
      this.chart1.setOption({
        //getDataset获取数据；
        // dataset: [that.dw.getDataset()[0]],
        dataset: [dataset],
        grid: {
          x: 0,
          y: 10,
          x2: 0,
          y2: 25
        },
        xAxis: {
          type: "category",
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            rotate: 0,
            interval: 0,
            fontSize: "66.7%",
            color: "#919BFF"
          }
        },
        yAxis: {
          type: "value",
          // interval: 10,
          minInterval: 1,
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: "dashed",
              color: "#4F5293"
            }
          },
          axisLabel: {
            fontSize: "66.7%",
            color: "#919BFF"
          }
        },
        tooltip: {},
        series: [
          {
            type: "bar",
            center: ["20%", "80%"], // 图的位置，距离左跟上的位置
            barMaxWidth: "30%", // 柱图宽度
            encode: {
              x: "name",
              y: "total"
            },
            itemStyle: {
              normal: {
                label: {
                  show: true, // 开启显示
                  position: "top", // 在上方显示
                  textStyle: {
                    // 数值样式
                    color: "#999999",
                    fontSize: "65%"
                  }
                },
                // 定义一个list，然后根据所以取得不同的值，这样就实现了，
                color: function(params) {
                  // 设置主题
                  let theme = "";
                  for (var i in themeObj) {
                    if (that.theme == themeObj[i].type) {
                      theme = themeObj[i].color;
                    } else {
                      theme = themeObj.defaultTheme.color;
                    }
                  }
                  return theme[params.dataIndex];
                  // var colorList = that.theme === 'lightTheme' ? lightTheme.color : defaultTheme.color
                }
              }
            }
          }
        ]
      });

      this.chart2 = echarts.init(
        that.$refs[`${that.chartName2}`]
        // that.dw.getTheme()
      );

      // 设置图表配置
      // const clientlabels = [];
      // const legendData = [];
      // dataset.source.forEach(item => {
      //   clientlabels.push(item[0]);
      //   legendData.push(item[1]);
      // });
      this.chart2.setOption({
        tooltip: {},
        color: ["rgba(100, 114, 255, 1)", "RGBA(28, 255, 240, 1)"],
        series: [
          {
            type: "pie",
            center: ["50%", "60%"],
            labelLine: {
              show: true
            },
             label: {
              show: true,
              formatter: function(params){
                if(that.total){
                   return params.name + Math.round(params.value/that.total*100) + '%'
                }
                   return  params.name  + '0%'
              },
            },
            radius: "65%",
             data: [
                {value: that.man, name: '男生'},
                {value: that.woman, name: '女生'},
            ]
          }
        ]
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function(el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    }
  }
};
</script>
<style lang="scss" scoped>
$commonColor: #c8cdff;
$themeColor: #919bff;
$textColore: #e768ff;
$textColor1: #1cfbec;
$textColor6: #6a65ff;
.textColore {
  color: $textColore;
}
.textColor1 {
  color: $textColor1;
}
.textColor6 {
  color: $textColor6;
}

.studentNumberStatistics {
  width: 100%;
  height: 100%;
  background: url("../../assets/imgs/school/wrap.png") no-repeat top center/100%
    100%;
  box-sizing: border-box;
  padding: 0.2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  .info {
    display: flex;
    justify-content: center;
    i.iconfont {
      font-size: 0.4rem;
      margin-right: 0.12rem;
    }
    .male {
      margin-right: 0.7rem;
      display: flex;
      color: #c8cdff;
      font-size: 0.12rem;
      align-items: center;
      .iconfont {
        color: #06d2c1;
      }
      .num {
        color: $textColor1;
        span {
          font-size: 0.24rem;
        }
      }
    }
    .female {
      display: flex;
      color: #c8cdff;
      font-size: 0.12rem;
      align-items: center;
      .iconfont {
        color: #6472ff;
      }
      .num {
        color: #6472ff;
        span {
          font-size: 0.24rem;
        }
      }
    }
  }
  .wrap {
    flex: 1;
    display: flex;
    .wrap-left {
      // padding: 0.2rem;
      flex: 1;
      display: flex;
      flex-direction: column;
      .wrap-left-top {
        padding: 0.1rem 0;
        height: 1.2rem;
        background-color: rgba($color: #6472ff, $alpha: 0.1);
        border-radius: 2px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 0.2rem;
        .num {
          font-size: 0.14rem;
          color: #8488b3;
          span {
            font-family: zcoolqingkehuangyouti;
            color: #c8cdff;
            font-size: 0.48rem;
          }
        }
        .ch {
          font-size: 0.16rem;
          color: #e5e5e5;
        }
        .eh {
          font-size: 0.12rem;
          color: #8488b3;
        }
      }
      .wrap-left-center {
        display: flex;
        justify-content: space-between;
        padding: 0.2rem 0;
        & > div {
          display: flex;
          .num {
            font-size: 0.24rem;
          }
          .txt {
            color: #c8cdff;
            font-size: 0.12rem;
          }
        }
      }
      .wrap-left-bottom {
        position: relative;
        flex: 1;
      }
    }
    .wrap-right {
      padding: 0 0.2rem;
      flex: 1;
      display: flex;
      flex-direction: column;
      .right-bottom {
        position: relative;
        flex: 1;
      }
    }
  }
}
.title {
  padding: 0.2rem 0;
  font-size: 0.18rem;
  color: $commonColor;
  img {
    width: 0.15rem;
    height: 0.14rem;
    margin-right: 0.06rem;
  }
  span {
    font-size: 0.18rem;
  }
  .seven {
    margin-left: 0.06rem;
    font-size: 0.12rem;
  }
}

.chart {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

.el-carousel {
  /deep/.el-carousel__indicator--horizontal {
    margin: 0 !important;
    padding: 0 !important;
    margin: 0 0 0 0.08rem !important;
    .el-carousel__button {
      width: 0.06rem !important;
      height: 0.06rem !important;
      border-radius: 0.06rem !important;
    }
    .el-carousel__button /deep/.el-carousel__indicator.is-active button {
      background-color: $commonColor !important;
    }
  }
  /deep/.el-carousel__indicator--vertical {
    margin: 0 !important;
    padding: 0 !important;
    // margin: 0 0 0 0.04rem !important;
    .el-carousel__button {
      width: 0.04rem !important;
      height: 0.8rem !important;
      background-color: #6472ff !important;
    }
    .el-carousel__button:nth-child(1) {
      border-radius: 0.04rem 0.04rem 0 0 !important;
    }
    .el-carousel__button:nth-last-child(1) {
      border-radius: 0 0 0.04rem 0.04rem !important;
    }
    // .el-carousel__indicator .el-carousel__indicator--vertical is-active{
    //   button {
    //     background-color: #6472FF !important;
    //   }
    // }
    .el-carousel__button {
      /deep/.el-carousel__indicator .is-active button {
        background-color: #6472ff !important;
      }
    }
  }
}
</style>
