export default class DateUtil {
  static date (interval = 0, current = null) {
    const d = DateUtil.current(current)
    d.setDate(d.getDate() + interval)
    return DateUtil.formatDate(d)
  };

  static week (interval = 0, current = null) {
    const d = DateUtil.current(current)
    if (d.getDay() === 0) {
      // Sunday
      d.setDate(d.getDate() - 6)
    } else {
      d.setDate(d.getDate() - (d.getDay() - 1))
    }
    d.setDate(d.getDate() + interval * 7)
    const end = new Date(d)
    end.setDate(d.getDate() + 6)
    return [DateUtil.formatDate(d), DateUtil.formatDate(end)]
  };

  static month (interval = 0, current = null, _internal = true) {
    let d = DateUtil.current(current)
    let year = d.getFullYear() + parseInt(interval / 12)
    let month = d.getMonth() + (interval % 12)
    if (month > 11) {
      month -= 12
      year += 1
    } else if (month < 0) {
      month += 12
      year -= 1
    }
    // 指定月份的第一天
    d = new Date(year, month, 1)
    let end = d
    if (_internal) {
      // 递归计算下个月的第一天
      const _ = DateUtil.month(1, d, false)
      end = new Date(_[0])
      end.setDate(end.getDate() - 1)
    }

    return [DateUtil.formatDate(d), DateUtil.formatDate(end)]
  };

  static semester (interval = 0, current = null) {
    let d = DateUtil.current(current)
    let end
    const month = d.getMonth()
    if (month >= 2 && month < 8) {
      // 3-8月，第二学期
      d = new Date(d.getFullYear(), 2, 1)
      end = new Date(d.getFullYear(), 7, 31)
    } else if (month < 2) {
      end = new Date(d.getFullYear(), 2, 1)
      end.setDate(end.getDate() - 1)
      d = new Date(d.getFullYear() - 1, 8, 1)
    } else {
      d = new Date(d.getFullYear(), 8, 1)
      end = new Date(d.getFullYear() + 1, 2, 1)
      end.setDate(end.getDate() - 1)
    }

    return [DateUtil.month(interval * 6, d)[0], DateUtil.month(interval * 6, end)[1]]
  };

  static year (interval = 0, current = null) {
    let d = DateUtil.current(current)

    d = new Date(d.getFullYear() + interval, 0, 1)
    const end = new Date(d.getFullYear(), 11, 31)
    return [DateUtil.formatDate(d), DateUtil.formatDate(end)]
  };

  static current (current = null) {
    if (current === null) {
      return new Date()
    }
    return new Date(current)
  };

  static formatDate (d) {
    return new Date(d.getTime() - d.getTimezoneOffset() * 60 * 1000).toISOString().split('T')[0]
  };
};
