<template>
  <div class="studentPhysiqueStatistics" :ref="chartName">
    <div v-show="isShowChart1" class="chart" :ref="chartName1"></div>
    <div v-if="!isShowChart1" class="noData">
      <img src="../../assets/imgs/index/nodata.png" alt="" />
      <p>暂无数据</p>
    </div>
  
  </div>
</template>

<script>
import * as echarts from "echarts";
import Api from "../../utils/api";
import DW from "../../../static/lib/DW";

export default {
  components: {},
  // props: ['uid'],
  data() {
    return {
      chartName1: this.chartName + "1",
      chart1: this.chartName + "1",
      isShowChart1: true,
      dw: null,
      sid: this.sid,
      provider_id:this.provider_id,
      infoData:'',
      type:1
    };
  },

  created() {
    // this.getUid()
  },
  mounted: function() {
    let that = this;
    //创建实例 组件ref,api根路径,鉴权token 必须
    that.dw = new DW(`${that.chartName}`, that.base_url, that.token);
    //请求数据 必须
    that.dw.load_data = async function() {
      // let assetsData = await that.main_statistic();
      // let consumeData = await that.consume_statistic();
      let assetsData = [];
      let consumeData = [];
      return new Promise(resolve => {
        resolve([assetsData, consumeData]);
      });
    };
    //渲染页面 必需
    that.dw.render = async function() {
      //除了绘制图表其他的事
      // that._doFn();
      //绘制图表
      that._drawLine();
    };
    //定义主题 非必须
    that.dw.getThemes = function() {
      return [
        { id: "dark", name: "暗色主题" },
        { id: "light", name: "亮色主题" }
      ];
    };
  },
  props: {
    theme: {
      type: String
    },
    chartName: {
      type: String
    },
    base_url: {
      type: String,
      default: ""
    },
    token: {
      type: String,
      default: ""
    }
  },
  computed: {},

  methods: {
    // getUid () {
    //   Bus.$on('personalUid', (e) => {
    //     this.uid = e
    //     this.getInfo()
    //   })
    // },
    async getData() {
      const that = this;
      return new Promise(resolve => {
        Api.GetStudentHealth.getApiData(
          "getStatistics",
          { sid: this.sid, provider_id: that.provider_id,type:that.type },
          function(data) {
            if(data.dataset.source.length===0){
              that.isShowChart1 = false
            }
            resolve(data.dataset);
          }
        );
      });
    },
    async _drawLine() {
      let that = this;
      let dataset = await that.getData();
      //getTheme获取主题；
      this.chart1 = echarts.init(
        that.$refs[`${that.chartName1}`],
        // that.dw.getTheme()
      );

      // 设置图表配置
      const clientlabels = [];
      const legendData = [];
      dataset.source.forEach(item => {
        clientlabels.push(item[0]);
        legendData.push(item[1]);
      });
      this.chart1.setOption({
        //getDataset获取数据；
        // dataset: [that.dw.getDataset()[0]],
        dataset: [dataset],
        title: {
          text: "学生体质计划统计",
          show: true,
          textStyle: {
            color: "#C8CDFF",
            fontSize: 12,
            fontWeight: "normal"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: function(params){
            return '学段：' +params['value'][0]+"\n" + params['value'][3]+":" + params['value'][2]+ '人'
          }
        },
        legend: {
          show: true,
          // align: "top",
          // orient: "horizontal",
          // icon: "circle",
          // itemWidth: 12,
          // left: "0",
          // top: "10%",
          // // formatter: { 'c'},
          // formatter: function(name) {
          //   var index = 0;
          //   var clientcounts = legendData;
          //   var clientlabels1 = clientlabels;
          //   clientlabels1.forEach(function(value, i) {
          //     if (value === name) {
          //       index = i;
          //     }
          //   });
          //   return "优秀";
          // },
          // textStyle: {
          //   color: "#C8CDFF",
          //   padding: [12, 0, 10, 20],
          //   fontSize: "80%"
          // }
        },
        series: [
          {
            type: "pie",
            name:null,
            roseType: "angle",
            center: ["50%", "65%"],
            radius: ["25%", "40%"],
            encode: {
              value:'number'
            },
            label: {
              show: true,
              formatter: "{c} ({d}%)"
            }
          }
        ]
      });
    },
    // 初始化 必须
    init() {
      this._dataInit();
      this.dw.resetDataset().init();
    },
    // 重渲染 必须
    reload() {
      this._dataInit();
      this.dw.render();
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 更改主题 必须
    changeTheme(theme) {
      let that = this;
      let blo = false;
      this.dw.getThemes().forEach(function(el) {
        if (el.id == theme) {
          blo = true;
        }
      });
      if (blo) {
        that.dw.setTheme(theme);
        try {
          that.chart1.dispose();
          that.chart2.dispose();
        } catch (e) {}
        that.dw.resetDataset("").init();
      } else {
        console.log(`${theme}该主题不存在`);
      }
    },
    // 设置其他参数 必须
    setSettings(obj) {
      this.dw.setSettings(obj);
    },
    // 设置机构id 必须
    setRangeId(obj) {
      this.dw.setRangeId(obj);
    },
    // 设置时间范围 必须
    setDateRange(obj) {
      this.dw.setDateRange(obj);
    },
    // 设置mock数据 必须
    setMock(mock) {
      this.dw.setMock(mock);
    },
    // 销毁图表及数据初始化 必须
    _dataInit() {
      try {
        this.chart1.dispose();
        this.chart2.dispose();
      } catch (e) {}
    }
  }
};
</script>
<style lang="scss" scoped>
$commonColor: #c8cdff;
$themeColor: #919bff;
$textColorg: #06d2c1;
$textColorr: #ff8d8e;
$textColorb: #6472ff;
.textColorg {
  color: $textColorg;
}
.textColorr {
  color: $textColorr;
}
.textColorb {
  color: $textColorb;
}
.studentPhysiqueStatistics {
  width: 100%;
  height: 100%;
  background: url("../../assets/imgs/school/wrapschool.png") no-repeat top
    center/100% 100%;
  box-sizing: border-box;
  padding: 0.2rem;
  position: relative;
  .type {
    display: flex;
    justify-content: space-between;
    .box {
      width: 1.46rem;
      //   height: 0.8rem;
      font-size: 0.16rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .numbs {
        font-size: 0.48rem;
      }
      .mold {
        font-size: 0.12rem;
        color: #919bff;
      }
    }
  }
}
.title {
  padding: 0.2rem 0;
  font-size: 0.18rem;
  color: $commonColor;
  img {
    width: 0.15rem;
    height: 0.14rem;
    margin-right: 0.06rem;
  }
  span {
    font-size: 0.18rem;
  }
  .seven {
    margin-left: 0.06rem;
    font-size: 0.12rem;
  }
}

.chart {
  box-sizing: border-box;
  padding-top: 0.2rem;
  width: 100%;
  height: 4rem;
}

.el-carousel {
  /deep/.el-carousel__indicator--horizontal {
    margin: 0 !important;
    padding: 0 !important;
    margin: 0 0 0 0.08rem !important;
    .el-carousel__button {
      width: 0.06rem !important;
      height: 0.06rem !important;
      border-radius: 0.06rem !important;
    }
    .el-carousel__button /deep/.el-carousel__indicator.is-active button {
      background-color: $commonColor !important;
    }
  }
  /deep/.el-carousel__indicator--vertical {
    margin: 0 !important;
    padding: 0 !important;
    // margin: 0 0 0 0.04rem !important;
    .el-carousel__button {
      width: 0.04rem !important;
      height: 0.8rem !important;
      background-color: #6472ff !important;
    }
    .el-carousel__button:nth-child(1) {
      border-radius: 0.04rem 0.04rem 0 0 !important;
    }
    .el-carousel__button:nth-last-child(1) {
      border-radius: 0 0 0.04rem 0.04rem !important;
    }
    // .el-carousel__indicator .el-carousel__indicator--vertical is-active{
    //   button {
    //     background-color: #6472FF !important;
    //   }
    // }
    .el-carousel__button {
      /deep/.el-carousel__indicator .is-active button {
        background-color: #6472ff !important;
      }
    }
  }
}
</style>
